.NavigatorMobile {
    width: 100vw;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #fff;
    border-top: 1px solid #efefef;
    margin-top: 2em;
    z-index: 100;
}

.NavigatorMobile ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.NavigatorMobile i {
    font-size: 2em;
    color: black;
}

@media (min-width: 700px) {
    .NavigatorMobile {
        display: none;
    }
}


