.user-stats {
    border-top: 1px solid #efefef;
    border-bottom: 1px solid #efefef;
    margin-bottom: 2rem;
}

.user-stats_list {
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
    padding: .5rem 0;
    margin-bottom: 1.4rem;
    max-width: 900px;
    margin: 0 auto;
}

.user-stats_list-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 33.3%;
    -ms-flex-align: center;
        align-items: center;
    font-size: 1rem;
}

.user-stats_list-item_label {
    color: #999;
}

.user-stats_list-item_stat {
    margin-bottom: .1rem;
}

@media (min-width: 700px) {
    .user-stats {
        width: 100%;
    }
    .user-stats_list {
        width: 100%;
    }
}


