.comment-input {
    border-top: 1px solid #efefef;
}
  
.comment-input__input {
    -ms-flex: 1 1;
        flex: 1 1;
    height: 2em;
    border: none;
}

.comment-input__input::-webkit-input-placeholder {
    color: #cdcdcdbb;
}

.comment-input__input:-ms-input-placeholder {
    color: #cdcdcdbb;
}

.comment-input__input::-ms-input-placeholder {
    color: #cdcdcdbb;
}

.comment-input__input::placeholder {
    color: #cdcdcdbb;
}

.comment-input__input:focus {
    outline: none;
}

.comment-input__button {
    font-size: 13px;
    padding: 0 10px;
}
  


